import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import TreeGrid from 'react-gridtree';
import registerServiceWorker from './registerServiceWorker';
import Data from './data'

class App extends React.Component {

  constructor(props) {
    super(props)
    this.data = []
  }

  formatCurrency(value) {
    return value.toLocaleString('pt-BR', { minimumFractionDigits: 2 })
  }


  render() {
    return (
      <div className="mainApp">
        <h2>React-GridTree example</h2>
        <TreeGrid
          data={Data}
          options={{
            fields:[
                {property: "account", displayName: "Conta",width: "20%"},
                {property: "accountName", displayName: "Conta", width: "30%"},
                {property: "startBalance", displayName: "Inicial", widht: "12,5%", format: this.formatCurrency},
                {property: "debits", displayName: "Dbito", width: "12,5%", format: this.formatCurrency},
                {property: "credits", displayName: "Crdito", width: "12,5%", format: this.formatCurrency},
                {property: "endBalance", displayName: "Final", width: "12,5%", format: this.formatCurrency}
            ]
          }}
        />
      </div>
    )
  }
}

ReactDOM.render(<App />, document.getElementById('root'));
registerServiceWorker();



